.irs-line-mid, .irs-line-left, .irs-line-right, .irs-bar, .irs-bar-edge, .irs-slider {
  background: white; }

.irs {
  height: 24px; }

.irs-with-grid {
  height: 60px; }

.irs-line {
  height: 3px;
  top: 12px;
  border: 1px solid #e7e4e4;
  background-color: #ffffff; }

.irs-line-left {
  height: 3px;
  background-position: 0 -30px; }

.irs-line-mid {
  height: 3px;
  background-position: 0 0; }

.irs-line-right {
  height: 3px;
  background-position: 100% -30px; }

.irs-bar {
  height: 3px;
  top: 12px;
  background-position: 0 -60px;
  background: black; }

.irs-bar-edge {
  top: 12px;
  height: 3px;
  width: 9px;
  background-position: 0 -90px; }

.irs-shadow {
  height: 3px;
  top: 34px;
  background: #000;
  opacity: 0.25; }

.lt-ie9 .irs-shadow {
  filter: alpha(opacity = 25); }

.irs-slider {
  border-radius: 50%;
  // box-shadow: 0 1px 1px rgba(1, 2, 1, 0.05)
  // border: 1px solid #e7e4e4
  // background-color: #ffffff
  width: 12px;
  height: 12px;
  background-color: black;
  bottom: 4px;
  cursor: pointer;
  &.state_hover, &:hover {
    opacity: 0.77; }
  &:before {
    content: '';
    z-index: 1;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: black; } }

.irs-min, .irs-max {
  color: black;
  font: 400 14px/16px 'Clear Sans Bold', arial, sans-serif;
  text-shadow: none;
  top: 100%;
  letter-spacing: 0.35px;
 }  // background: #e1e4e9

.irs-from, .irs-to, .irs-single {
  color: #fff;
  font-size: 10px;
  line-height: 1.333;
  text-shadow: none;
  padding: 1px 5px;
  background: black;
  -moz-border-radius: 4px;
  border-radius: 4px; }

.irs-from:after, .irs-to:after, .irs-single:after {
  position: absolute;
  display: block;
  content: "";
  bottom: -6px;
  left: 50%;
  width: 0;
  height: 0;
  margin-left: -3px;
  overflow: hidden;
  border: 3px solid transparent;
  border-top-color: black; }

.irs-grid-pol {
  background: #e1e4e9; }

.irs-grid-text {
  color: #999; }

.irs-disabled {}

.irs {
  position: relative;
  display: block;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }
.irs-line {
  position: relative;
  display: block;
  overflow: hidden;
  outline: none !important; }
.irs-line-left, .irs-line-mid, .irs-line-right {
  position: absolute;
  display: block;
  top: 0; }
.irs-line-left {
  left: 0;
  width: 11%; }
.irs-line-mid {
  left: 9%;
  width: 82%; }
.irs-line-right {
  right: 0;
  width: 11%; }
.irs-bar {
  position: absolute;
  display: block;
  left: 0;
  width: 0; }
.irs-bar-edge {
  position: absolute;
  display: block;
  top: 0;
  left: 0; }
.irs-shadow {
  position: absolute;
  display: none;
  left: 0;
  width: 0; }
.irs-slider {
  position: absolute;
  display: block;
  cursor: default;
  z-index: 1;
  &.single {}
  &.from {}
  &.to {}
  &.type_last {
    z-index: 2; } }
.irs-min {
  position: absolute;
  display: block;
  left: 0;
  cursor: default; }
.irs-max {
  position: absolute;
  display: block;
  right: 0;
  cursor: default; }
.irs-from, .irs-to, .irs-single {
  position: absolute;
  display: block;
  top: -10px;
  left: 0;
  cursor: default;
  white-space: nowrap; }
.irs-grid {
  position: absolute;
  display: none;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 20px; }
.irs-with-grid {
  .irs-grid {
    display: block;
    display: none; } }
.irs-grid-pol {
  position: absolute;
  top: 0;
  left: 0;
  width: 1px;
  height: 8px;
  background: #000;
  &.small {
    height: 4px; } }
.irs-grid-text {
  position: absolute;
  bottom: 0;
  left: 0;
  white-space: nowrap;
  text-align: center;
  font-size: 9px;
  line-height: 9px;
  padding: 0 3px;
  color: #000; }
.irs-disable-mask {
  position: absolute;
  display: block;
  top: 0;
  left: -1%;
  width: 102%;
  height: 100%;
  cursor: default;
  background: rgba(0,0,0,0.0);
  z-index: 2; }
.lt-ie9 {
  .irs-disable-mask {
    background: #000;
    filter: alpha(opacity=0);
    cursor: not-allowed; } }
.irs-disabled {
  opacity: 0.4; }
.irs-hidden-input {
  position: absolute !important;
  display: block !important;
  top: 0 !important;
  left: 0 !important;
  width: 0 !important;
  height: 0 !important;
  font-size: 0 !important;
  line-height: 0 !important;
  padding: 0 !important;
  margin: 0 !important;
  outline: none !important;
  z-index: -9999 !important;
  background: none !important;
  border-style: solid !important;
  border-color: transparent !important; }
