.search {
  position: relative;
  max-width: 208px;
  &__field {
    color: #d6d6d6;
    letter-spacing: 0.35px;
    height: 33px;
    line-height: 33px;
    padding: 11px 36px 11px 13px;
    @extend %inputs;
    @include regular-italic; }
  &__btn {
    @include hide-text;
    background: url(data:image/png;base64,) center no-repeat;
    width: 19px;
    height: 19px;
    position: absolute;
    top: 46%;
    transform: translateY(-50%);
    right: 9px; } }
