.b-404 {
	background-color: $bg_gray;
	padding: 25px 0 54px 0;
	&__inner {
		max-width: 950px; }
	&__header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 46px; }
	&__col {
		&:first-of-type {
			max-width: 310px; }
		&:last-of-type {
			padding-top: 15px; } }
	&__title {
		font-size: 72px;
		color: #bcbcbc;
		@include bold;
		span {
			display: block;
			font-size: 170px;
			line-height: 140px;
			text-transform: uppercase; } }
	&__subtitle {
		font-size: 14px;
		padding: 21px 0 20px 6px;
		color: $black; } }

