//vars
$green: #6dca14;
$red: #e86278;
$white: #fff;
$black: #000;
$gray: #818181;
$gray2: #bdbdbd;
$bg_gray: #f3f3f3;

//fonts

/*=== import ===*/
@include font("Clear Sans Bold Italic", "ClearSans-Bolditalic");
@include font("Clear Sans Bold", "ClearSans-Bold");
@include font("Clear Sans Medium Italic", "ClearSans-Mediumitalic");
@include font("Clear Sans Medium", "ClearSans-Medium");
@include font("Clear Sans Regular", "ClearSans-Regular");
@include font("Clear Sans Italic", "ClearSans-Italic");
@include font("Clear Sans Thin", "ClearSans-Thin");
@include font("Clear Sans Light", "ClearSans-Light");

/*=== mixins ===*/
@mixin bold-italic {
	font-family: "Clear Sans Bold Italic", sans-serif;
	font-weight: normal; }
@mixin bold {
	font-family: "Clear Sans Bold", sans-serif;
	font-weight: normal; }
@mixin medium-italic {
	font-family: "Clear Sans Medium Italic", sans-serif;
	font-weight: normal; }
@mixin medium {
	font-family: "Clear Sans Medium", sans-serif;
	font-weight: normal; }
@mixin regular {
	font-family: "Clear Sans Regular", sans-serif;
	font-weight: normal; }
@mixin regular-italic {
	font-family: "Clear Sans Italic", sans-serif;
	font-weight: normal; }
@mixin thin {
	font-family: "Clear Sans Thin", sans-serif;
	font-weight: normal; }
@mixin light {
	font-family: "Clear Sans Light", sans-serif;
	font-weight: normal; }

// titles
.title {
	text-align: center;
	@include bold;
	font-size: 18px;
	text-transform: uppercase;
	letter-spacing: 3.6px;
	line-height: 1.2;
	color: $black;
	span {
		position: relative;
		display: inline-block;
		&:before {
			content: '';
			display: block;
			position: absolute;
			right: 100%;
			top: 50%;
			margin: -1px 20px 0 0;
			width: 75px;
			height: 2px;
			background: $black; } } }
.title-secondary {
	@include bold;
	font-size: 18px;
	color: $black;
	text-align: center;
	letter-spacing: 3.6px;
	text-transform: uppercase; }

//icons
.icon-arr {
	font-size: 6px;
	fill: $black; }
.icon-iblock-1 {
	font-size: 40px;
	fill: $red; }
.icon-arrd {
	font-size: 24px;
	fill: #bcbcbc; }
.icon-cancel {
	font-size: 16px;
	@include tr(opacity .3s ease); }
//basic styles
body {
	background: $white;
	@include css-lock(16, 50, 600, 1000);
	line-height: 1.4;
	@include regular;
	display: flex;
	flex-direction: column;
	min-height: 100vh; }
a {
	text-decoration: none;
	color: inherit; }
button {
	background: none;
	outline: none;
	border: 0; }
img {
	max-width: 100%;
	max-height: 100%; }
//buttons
.btn {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	height: 50px;
	padding: 5px 15px;
	position: relative;
	min-width: 200px;
	color: $white;
	font-weight: 400;
	font-size: 14px;
	line-height: 1.4;
	@include bold;
	text-transform: uppercase;
	letter-spacing: 1.4px;
	background: $red;
	transition: background 0.25s;
	&:hover {
		background: darken($red, 5%); }
	&:active {
		top: 1px; }
	&_primary {
		background: $green;
		&:hover {
			background: darken($green, 5%); } }
	&_secondary {
		background: $white;
		font-size: 12px;
		color: $black;
		&:hover {
			background: darken($white, 5%); }
		//	.icon
		//		transform: translateX(3px)
  //    //
		//.icon
		//	margin: 0 0 0 6px
 } }		//	transition: transform 0.25s
.btn-close {
	background-color: $gray2;
	border-radius: 50%;
	width: 31px;
	height: 31px;
	display: flex;
	display: block;
	align-items: center;
	&:hover {
		.icon-cancel {
			opacity: 0.7; } } }
// inputs
%inputs {
	width: 100%;
	outline: 0;
	display: block;
	border: 0; }

// checkbox
.checkbox {
	&__list {
		display: flex;
		flex-direction: column;
		padding-left: 5px; }
	&__item {
		position: relative;
		&:not(:last-child) {
			margin-bottom: 10px; } }
	&__field {
		position: absolute;
		z-index: -1;
		opacity: 0;
		height: 0;
		&:checked + label:after {
			content: "";
			z-index: 3;
			top: 39%;
			left: 2%;
			position: absolute;
			-ms-transform: translate(-50%,-50%) rotate(220deg);
			transform: translate(-50%,-50%) rotate(220deg);
			width: 8px;
			height: 8px;
			background: 0 0;
			border-top: 2px solid #000;
			border-left: 2px solid #000; } }
	&__label {
		display: flex;
		align-items: center;
		cursor: pointer;
		&:before {
			content: "";
			-webkit-user-select: none;
			-moz-user-select: none;
			-ms-user-select: none;
			user-select: none;
			overflow: hidden;
			-webkit-box-shadow: none;
			box-shadow: none;
			background: 0 0;
			width: 13px;
			height: 13px;
			border-radius: 3px;
			border: 1px solid #e4e4e4;
			background-color: #f3f3f3; } }
	&__title {
		font-size: 14px;
		letter-spacing: 0.35px;
		padding-left: 13px;
		color: $black; } }

// range-slider
.range-slider {
	padding-left: 10px;
	width: 90%;
	margin: 33px 0 53px 0;
	&__title,
	&__inputs {
		font-size: 14px;
		color: $gray;
		letter-spacing: 0.35px; }
	&__controls {
		display: flex;
		justify-content: space-between;
		width: 100%;
		margin: 0 -14px	15px; }
	&__col {
		padding: 0 14px; }
	&__input {
		width: 100%;
		display: block;
		min-width: 100px;
		color: $gray2;
		padding: 7px 11px;
		border-radius: 3px;
		background-color: #f3f3f3; } }

// pagination
.catalog-page {
	display: flex;
	font-size: 14px;
	text-transform: uppercase;
	justify-content: space-between;
	@include bold;
	&__navigation {
		display: flex;
		justify-content: center;
		align-items: center;
		&:last-of-type {
			.catalog-page__first,
			.catalog-page__link, {
				border-left-width: 1px;
				border-left-style: solid;
				border-color: $black; }
			.catalog-page__first {
				border-left: none; } } }
	&__title {
		letter-spacing: 0.7px; }
	&__current {
		color: $gray2; }
	&__link {
		color: $black;
		@include tr(color .25s ease);
		line-height: 13px;
		&:hover {
			color: $gray2; } }
	&__first,
	&__link {
		text-decoration: none;
		padding: 0 8px; } }
// catalog-list
.catalog-list {
	display: flex;
	justify-content: center;
	align-items: center;
	&__item {
		padding: 0 15px;
		flex: 1;
		display: flex; }
	&__link {
		color: $black;
		font-size: 14px;
		text-transform: uppercase;
		letter-spacing: 1.4px;
		border-bottom: 2px solid $black;
		@include bold;
		transition: border-color .25s ease, color .25s ease;
		padding: 10px 45px;
		&:hover,
		&.is-active {
			color: #fe405e;
			border-color: #fe405e; } } }
// select

.select {
	position: relative;
	border: 0;
	min-width: 237px;
	cursor: pointer;
	border-radius: 3px;
	background-color: #f3f3f3;
	letter-spacing: 0.35px;
	font-size: 14px;
	height: 33px;
	line-height: 33px;
	padding: 4px 10px;
	color: #0d0113;
	display: flex;
	&:after {
		content: '';
		display: block;
		position: absolute;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 4px 4.5px 0;
		border-color: #000 transparent transparent;
		bottom: 0;
		right: 5px;
		margin: auto; }
	&__wh {
		background-color: $white;
		min-width: 208px;
		margin-bottom: 0; } }
