// catalog-promo
.catalog-promo {
	@include cover;
	min-height: 315px;
	text-align: center;
	color: $white;
	position: relative;
	padding: 50px 0;
	&:before {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		z-index: 0;
		left: 0;
		background-color: rgba(12, 1, 19, 0.25);
		width: 100%;
		height: 100%; }
	&__in {
		max-width: 607px;
		z-index	1 {}
		position: relative; }
	&__title {
		letter-spacing: 1.05px;
		@include bold;
		text-shadow: 0 1px 0 #000000;
		font-size: 42px; }
	&__subtitle {
		font-size: 14px;
		line-height: 24px;
		margin: 14px 0 40px 0;
		letter-spacing: 0.35px; }
	&__btn {
		min-width: 240px;
		display: flex;
		margin: auto; } }


// cards
.cards {
	padding: 35px 0 127px 0;
	background-color: $bg_gray;
	&__in {
		max-width: 1147px;
		flex-direction: column; }
	&__top {
		padding-bottom: 6px;
		justify-content: space-between;
		border-bottom: 1px solid #dedede;
		align-items: flex-start;
		margin-bottom: 20px; }
	&__top,
	&__header,
	&__in,
	&__main,
	&__aside,
	&__list {
		display: flex; }
	&__aside {
		min-width: 348px;
		margin-right: 36px; }
	&__header {
		flex: 1 1 auto;
		justify-content: center;
		align-items: center;
		margin: 0 0 50px 100px; }
	&__main {
		flex-grow: 1;
		align-items: flex-start; }
	&__body {
		flex-grow: 0; }
	&__list {
		flex-wrap: wrap;
		justify-content: space-between;
		padding-top: 10px;
		padding-bottom: 7px;
		display: none;
		&.is-active {
			display: flex; } }
	&__item {
		flex-grow: 0;
		flex-shrink: 0;
		max-width: 350px;
		padding: 12px 0; } }
.card {
	&__top {
		position: relative; }
	&__slider {
		height: 221px;
		&:before {
			content: '';
			z-index: 2;
			display: block;
			@include coverdiv;
			background-color: rgba(12, 1, 19, 0.15); }
		.slick-list.draggable {
			overflow: hidden; }
		.slick-arrow {
			width: 65px;
			position: absolute;
			top: 48%;
			transform: translateY(-50%);
			padding: 20px;
			cursor: pointer;
			z-index: 10;
			transition: opacity 0.25s;
			@include hide-text;
			&:hover {
				opacity: 0.4; } }
		.slick-prev {
			left: 0%;
			&:before {
				content: '';
				display: block;
				position: absolute;
				@include vcenter;
				width: 14px;
				height: 21px;
				background: url(data:image/png;base64,) no-repeat center; } }
		.slick-next {
			right: 0%;
			&:before {
				content: '';
				display: block;
				position: absolute;
				@include vcenter;
				width: 14px;
				height: 21px;
				background: url(data:image/png;base64,) no-repeat center; } }
		.slick-dots {
			display: flex;
			justify-content: center;
			position: absolute;
			left: 0;
			right: 0;
			bottom: 10px;
			z-index: 10;
			li {
				width: 9px;
				height: 9px;
				background: rgba($white, 0.5);
				border-radius: 50%;
				overflow: hidden;
				margin: 0 5px;
				cursor: pointer;
				transition: background 0.25s;
				&:hover,
				&.slick-active {
					background: $white; } }
			button {
				@include hide-text; } } }
	&__link {
		@include coverdiv;
		z-index: 4; }
	&__content {
		padding: 10px 20px 20px 16px;
		background-color: $white; }
	&__title {
		font-size: 24px;
		@include bold; }
	&__l {
		color: $gray;
		margin: 10px 0 23px 0;
		font-size: 18px; }
	&__footer {
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-top: 1px solid #e9e9e9;
		padding-top: 19px; }
	&__btn {
		min-width: 180px;
		font-size: 10px; } }
.card__slider-item {
	position: relative;
	overflow: hidden;
	width: 350px;
	height: 221px;
	img {
		@include coverdiv;
		object-fit: cover; } }


.price-wrp {
	box-shadow: 0 12px 24px rgba(28, 28, 28, 0.15);
	background-color: $black;
	display: flex;
	min-width: 210px;
	align-items: center;
	height: 49px;
	position: absolute;
	padding: 13px 20px;
	top: 16%;
	left: 26%;
	transform: translate(-50%, -50%);
	z-index: 3; }
.price {
	line-height: 39.44px;
	text-transform: uppercase;
	@include bold;
	&__bl {
		color: $black;
		font-size: 18px; }
	&__wh {
		font-size: 30px;
		color: $white; } }



// objects
.objects {
	display: flex;
	font-size: 14px;
	text-transform: uppercase;
	justify-content: center;
	color: $black;
	@include bold;
	max-width: 485px;
	padding-top: 8px;
	&__title {
		letter-spacing: 0.7px; }
	&__col {
		flex: 1;
		display: flex;
		flex-wrap: wrap; }
	&__link {
		font-size: 11px;
		letter-spacing: 0.5px;
		padding: 3px 5px;
		color: $black;
		cursor: pointer;
		@include tr(color .25s ease);
		&:not(:first-child) {
			&:hover,
			&.is-active {
				color: $red; } } }
	&__current {
		color: $gray2; } }


// aside
.aside {
	flex-direction: column;
	&__col {
		margin-bottom: 20px; }
	&__title {
		font-size: 18px;
		@include bold;
		text-transform: uppercase;
		letter-spacing: 0.9px;
		padding-bottom: 14px;
		border-bottom: 1px solid $gray2;
		margin-bottom: 25px; }
	&__top {
		margin-bottom: 23px;
		position: relative; }
	&__body {
		padding: 20px;
		background-color: $white;
		box-shadow: 0 10px 49px rgba(28, 28, 28, 0.15); }
	&__col {
		margin-bottom: 20px; }
	&__content {
		padding-right: 36px;
		margin-bottom: 40px; }
	&__btns {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-left: 10px; }
	h5 {
		font-size: 14px;
		color: $gray;
		letter-spacing: 0.35px;
		margin-bottom: 10px; } }
.aside__top-title {
	color: $gray2;
	font-size: 14px;
	letter-spacing: 0.35px;
	position: absolute;
	bottom: 110%; }
